@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --white: floralwhite;
  --ham-color: cyan;
  --offWhite: #f7f7f7;
  --mainBlack: #222;
  --offBlack: #000000;
  --mainGrey: #ececec;
  --darkGrey: #c4c4c4;
  --brand-color: #47a4de;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 3px;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}

body {
  font-family: "Nunito", sans-serif, "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000000;
  background: var(--offBlack);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

section {
  padding: 70px 20px;
  color: floralwhite;
  color: var(--white);
}

section p {
  font-size: 18px;
  font-weight: normal;
}



.defaultHero {
  height: 100vh;
  width: 100%;
  min-height: calc(100vh-66px);
  background: url(/wissensof/static/media/hero.1c2d942b.jpg) center center/cover no-repeat;
  text-align: center;
  position: relative;
  top: -72px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.3);
  object-fit: contain;
}

.defaultHero .scrolldown a {
  position: absolute;
  bottom: 30px;
  left: 45%;
  /* margin-left: 29px; */
  color: var(--darkGrey);
  display: block;
  height: 50px;
  width: 50px;
  font-size: 35px;
  line-height: 42px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 8px;
  transition: 0.3s;
}

.defaultHero .scrolldown a:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.smoothscroll > .angle_down {
  width: 1.95px !important;
}

.banner {
  width: 85%;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 28%;
  /* padding-bottom: 30px; */
  text-align: left;
  /* background: rgba(0, 0, 0, 0.5); */
  color: var(--white);
  font-size: 67px;
  letter-spacing: 5px;
  padding: 8px;
}


.title {
  display: flex;
  justify-content: space-between;
}
.title h4 {
  color: var(--white);
  font-style: normal;
  font-weight: normal;
  font-size: 44px;
  line-height: 56px;
}

.projects {
  margin-top: 50px;
  color: var(--white);
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttons__back {
  margin: 0 2rem;
}

.control__btn {
  height: 40px;
  width: 40px;
  border: 2px solid #f2f2f2;
  background: transparent;
  border-radius: 50%;
  cursor: pointer;
  transition: transform ease-in 0.1s;
}

.control__btn:hover {
  transform: scale(1.1);
}

.control__btn:focus,
.control__btn:active {
  outline: none;
  border: 2px solid var(--brand-color);
}

i:focus {
  outline: 0;
}
.slide {
  border: none;
  overflow: visible;
}
.slide__card {
  border: none;
  margin: 0 0.4rem !important;
  border-bottom: 2px solid #4f4f4f !important;
}

.slide__cardbody {
  background: var(--offBlack);
  padding: 1rem 0 !important;
  color: floralwhite;
}

.slider__dots {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider__dot {
  margin-right: 8px;
  border-radius: 50%;
  background: #4f4f4f;
  width: 9px;
  height: 9px;
  cursor: pointer;
  border: #4f4f4f;
}

.slider__dot:active,
.slider__dot:focus {
  background: var(--brand-color);
}

.slider__dot:active > i,
.slider__dot:focus > i {
  color: var(--brand-color);
}

.slide__wiss__info {
  background-color: var(--offBlack);
  position: absolute;
  bottom: -5%;
  left: 0;
  right: 0;
  height: 55px;
  width: 100%;
  padding: 10px 10px 10px 0;
}

@media only screen and (max-width: 991px) {
  .slide__cardbody {
    background: var(--offBlack);
    padding: 0.2rem 0 !important;
  }
  .control__btn {
    height: 35px;
    width: 35px;
  }
}

.contact {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: var(--white);
}

.contact h5 {
  font-size: 18px;
}

.contact__details {
  margin: 80px 0 160px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contact__social {
  margin-bottom: 40px;
}

.contact__email {
  margin-top: 40px;
}

.team {
  margin-top: 50px;
  color: var(--white);
}

.navbar {
  background-color: rgba(0, 0, 0, 0.4);
  height: 72px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar__container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 72px;
  max-width: 1500px;
}

.navbar__logo {
  position: absolute;
  left: 0;
  top: 10%;

  transform: translate(20%, 10%);
}

.nav__menu {
  /* display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem; */
  left: -100%;
  list-style: none;
}

.nav__links {
  display: none;
  color: var(--white);
  text-decoration: none !important;
  padding: 0.5rem 1rem;
}

.nav__linksMobile {
  display: none;
}

.nav__menu.active {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  /* justify-content: center; */
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 72px;
  left: 0;
  -webkit-backdrop-filter: blur(100px);
  backdrop-filter: blur(100px);
  transition: all 0.5s ease;
  z-index: 1;
}
.nav__menu.active .nav__links {
  color: var(--white);
  font-size: 24px;
  padding: 16px;
  /* line-height: 22px; */
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border-bottom: 1px solid transparent;
  padding-bottom: 3px;
  font-weight: 500;
  transform: scale(0.9);
  transition: 0.4s ease-in-out;
}

.nav__menu.active .nav__links:hover {
  transform: scale(1.1);
  color: var(--brand-color);
}

/* .wissnav__styledHamburger {
  display: none;
} */

.wissnav__styledHamburger {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: 0px none;
  background-color: transparent;
  /* display: block; */
  position: absolute;
  top: 10%;
  right: 0;
  transform: translate(-100%, 60%);
  font-size: 1.8rem;
  cursor: pointer;
}

.wissnav__hamburgerBox {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 24px;
}

.wissnav__hamburgerInner {
  background-color: var(--white);
  position: absolute;
  width: 25px;
  height: 2px;
  border-radius: 2px;
  top: 50%;
  left: 0px;
  right: 0px;
  transition: transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0s;
  transform: rotate(0deg);
}

.wissnav__hamburgerInner::before {
  width: 100%;
  top: -10px;
  opacity: 1;
  transition: top 0.1s ease-in 0.25s, opacity 0.1s ease-in 0s;
}

.wissnav__hamburgerInner::before,
.wissnav__hamburgerInner::after {
  content: "";
  display: block;
  background-color: var(--white);
  position: absolute;
  left: auto;
  right: 0px;
  /* width: 30px; */
  height: 2px;
  /* transition-timing-function: ease;
   transition-duration: 0.15s;
   transition-property: transform; */
  border-radius: 4px;
}

.close__btn {
  position: absolute;
  width: 25px;
  left: 0px;
  right: 0px;
  top: -10px;
  font-size: 30px;
  color: var(--white);
  /* z-index: 2; */
  cursor: pointer;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
  background: #121212;
  color: #fff;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
}

